.menuContainer {
  display: flex;
  width: 100vw;
  flex-direction: row;
  background: white;
  border-bottom: 2px solid rgba(22, 22, 22, 0.694);
  -webkit-box-shadow: 0px 0px 7px 3px rgba(0, 0, 0, 0.38);
  box-shadow: 0px 0px 7px 3px rgba(0, 0, 0, 0.38);
  font-family: "Montserrat", serif;
  text-shadow: 0.2px 0.2px 0.2px #aaa;
}

.logoImg {
    /* width: 250px; */
    /* height: 60px; */
    margin-left: 50px;
    margin-top: 10px;
    margin-bottom: 5px;
}

.menu {
  text-decoration: none;
  display: flex;
  width: 80vw;
  justify-content: flex-end;
  padding-top: 15px;
  height: 50px;
}

@media screen and (min-width: 850px) and (max-width: 950px) {
  .menu{
    width: 88vw;
    display: flex;
  }
  .logoImg {
    width: 90px;
    height: 20px;
    margin: 27px 10px 7px 5px;
  }
 
  .menuItem{
    font-size: 10px;
    margin: 0 2px;
    align-items: center;
    justify-content: flex-end;
    padding: 5px;
  }
}


.menuItem {
  display: flex;
  margin: 0 10px;
  text-decoration: none;
  border: none;
  background: none;
  height: 35px;
  outline: none;
  font-family: "Montserrat", serif;
  font-weight: 700;
  font-size: 15px;
  padding: 5px 10px;
  justify-content: center;
  align-items: flex-end;
  vertical-align: bottom;
}

.menuItem:hover {
  background: #333;
  margin: 0 10px;
  color: #fff;
  height: 35px;
  border: none;
  outline: none;
  padding: 5px 10px;
  justify-content: center;
  font-weight: 700;
  font-size: 15px;
  background: linear-gradient(to right, #fff 50%, #333 50%);
  background-size: 200% 100%;
  background-position: right bottom;
  transition: all 0.2s ease-out;
}

.menuModels {
  width: 95%;
  z-index: 1;
  background: rgb(248, 247, 247);
  margin-top: 70px;
  display: flex;
  /* padding-top: 20px ; */
  padding-left: 90px;
  position: absolute;
  left: 0;
  -webkit-box-shadow: 0px 0px 7px 3px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 0px 7px 3px rgba(0, 0, 0, 0.1);
}

.menuModelsHidden {
  position: absolute;
  top: -1000px;
  z-index: -100;
  overflow: hidden;
  display: flex !important;
  color: white;
  width: 100vw;
  display: block;
}

.modelsBrand {
  display: flex;
  flex-wrap: wrap;

  /* border-right: 1px solid silver; */
}
.modelsBrand:last-child {
  border-right: none;
}

