    margin: 20px auto;
} */

.modelImg {
  width: 60px;
  vertical-align: center;
  align-self: center;
}

.modelDescription {
  text-align: center;
}

.modelItem {
  width: 180px;
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid silver;
  padding: 10px;
  margin: 0;
  font-size: 14px;
}

.modelItem:last-child {
  border-bottom: none;
}

.modelItem:hover {
  font-weight: bold;
  text-shadow: #aaa;
  color: rgb(9, 9, 226);
}


.mobileMenu{
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 5px;
  top: 15px;
  background: none;
  border: none;
  outline: none;
}

.mobile_menu{
  width: 1.8rem;
  height: 1px;
  margin: .2rem;
  border-bottom: 2px solid rgb(15, 15, 15);
}

.mobileMenu:hover >.mobile_menu {
   border-bottom: 2px solid rgb(16, 16, 170);
}

.hideMenu{
  display: none;
}

.showMenu{
  border-top: 1px solid silver;
  display: flex;
  flex-direction: column;
  height: 91vh;
  background: white;
  width: 100%;
   position: fixed;
   top: 9vh;
   left: 0;
   z-index: 100;
}